// A beautiful selection of our branded colors
//
// Styleguide

// primary colors
$primary:       #E83FB8;
$primary-dark:  darken($primary, 5%);

$link:          #4076D4;
$link--visited: #9140D4;
$link--hover:   darken($link, 10%);
$link--active:  #D44076;

// dark shades
$gray1:         #3c3c3d;
$gray2:         #5a5a5b;
$gray3:         #7d7d7e;

// light shades
$gray4:         #cdcdce;
$gray5:         #dededf;
$gray6:         #efeff0;

$white:         #ffffff;

// Font based variables
//
// Styleguide

$base-font:       'Helvetica Neue','Segoe UI', 'Verdana', sans-serif;
$monospace-font:  'Consolas', 'Monaco', 'Courier New', monospace;

$heading:         $base-font;
$body:            $base-font;
$monospace:       $monospace-font;

$font-size: 2.2rem;
$line-height: 1.5;

$font-size--small: $font-size * 0.75;
$font-size--large: $font-size * 1.25;
@import "cutestrap/dist/scss/variables/z_index";